import axios from "axios";
var URL = process.env.REACT_APP_API_NEWS_key;

export function fetchNews() {
  // console.log(URL);
  var request = axios
    .get(
      "https://newsapi.org/v2/top-headlines?q=covid&country=in&apiKey=8848e02cd0aa4335bc396eb593f1ce71"
    )
    .then(res => {
      //   console.log(res);
      return res.data.articles;
    })
    .catch(err => console.log(err));
  return {
    type: "NEWS",
    payload: request
  };
}
